/// <reference path="../_references.d.ts"/>
import {SettingsResource} from "../Resources/settings.resource";

/**
* @ngdoc helper
* @name BlueLeet.UCodeFirstDashboardController
* @function
*
* @description
* Defines a dashboard controller for UCodeFirst.
*/
export class UCodeFirstDashboardController {
    ucodeFirstVersion: string = null;
    loaded: boolean = false;

    $inject = ["$routeParams", "blSettingsResource"];
    constructor(protected $routeParams, protected blSettingsResource: SettingsResource) {
        blSettingsResource.getUCodeFirstVersion().success(response => {
            this.ucodeFirstVersion = response;
            this.loaded = true;
        });
    }
}



